<template>
    <div v-loading="loading">
      <div v-if="preview && fileList.length > 0" class="input-file-preview space-y-4">
        <slot name="preview">
          <div class="w-full" v-for="(file, fileIndex) in fileList" :key="fileIndex">
            <div class="file-card">
              <div class="flex gap-4 items-center">
                <span class="material-icons icon-md">
                  {{ getFileIcon(file.type || getMimeFromName(file.file)) }}
                </span>
                <span class="text-black">
                  {{ file.name || getFileName(file.file) }}
                </span>
                <span class="text-sm">
                  {{ file.size ? formatFileSize(file.size) : '' }}
                </span>
                <span class="text-grey-02" v-if="file.created_at">
                  Uploaded on {{ formatDate(file.created_at) }}
                </span>
              </div>
  
              <Dropdown trigger="click" hide-on-click placement="bottom-end" class="mr-8">
                <Button :loading="loading" color="teal" view="outlined" class="btn-action" dense icon>
                    <span class="material-icons">more_horiz</span>
                </Button>
                <template #dropdown>
                  <DropdownMenu class="py-1 text-sm">
                    <DropdownItem class="text-base" v-if="getFileExtension(file.file) == 'pdf'">
                        <a :href="file.file" target="_blank" class="text-grey-03">
                            <span class="material-icons mr-2">visibility</span> Preview
                        </a>
                    </DropdownItem>
                    <DropdownItem class="text-base" @click="downloadFile(file)">
                      <span class="material-icons mr-2">download</span> Download
                    </DropdownItem>
                    <DropdownItem class="text-base" @click="removeFile(file.id)">
                      <span class="material-icons mr-2">delete</span> Remove
                    </DropdownItem>
                  </DropdownMenu>
                </template>
              </Dropdown>
            </div>
          </div>
        </slot>
      </div>
  
      <div
        v-else
        :class="[
          'input-file', 
          disabled ? 'opacity-30' : '',
          isDragging ? 'dragging' : ''
        ]"
        @dragenter.prevent="handleDragEnter"
        @dragover.prevent="handleDragOver"
        @dragleave.prevent="handleDragLeave"
        @drop.prevent="handleDrop"
      >
        <input
          ref="fileInput"
          type="file"
          :accept="acceptedFileTypes"
          class="input-file-form"
          multiple
          :disabled="disabled"
          @change="handleFileChange"
        />
        <div class="my-12">
          <slot>
            <div class="flex flex-col justify-center items-center text-center">
              <span class="material-symbols-outlined icon-xl">upload</span>
              <strong class="text-xl">Choose files or drag them here</strong>
              <p class="text-base text-grey-02 font-light">Upload PDF, CSV, or XLSX files</p>
              <Button class="mt-4" color="teal" view="outlined" @click.prevent="triggerFileInput">
                Upload Files
              </Button>
            </div>
          </slot>
        </div>
      </div>
    </div>
    <small
      ><b>File</b>:
    {{
      rules.allowedFiles.map((allowedFile) => allowedFile.name).join(', ')
    }}</small>
  </template>
  
  <script setup>
  import { ref, computed, defineProps, defineEmits, defineExpose } from 'vue'
  import Button from 'bbUi/components/button/button.vue'
  import { ElMessage } from 'element-plus'
  
  const props = defineProps({
    data: {
      type: Object,
      default: () => ({
        id: Math.floor(Math.random() * 1000000),
        nad: [],
        custom_offer: []
      })
    },
    preview: {
      type: Boolean,
      default: true
    },
    rules: {
      type: Object,
      default: () => ({
        maxFileSize: '50MB',
        allowedFiles: [
          { name: 'PDF', mime: 'application/pdf' },
          { name: 'CSV', mime: 'text/csv' },
          { name: 'XLSX', mime: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }
        ]
      })
    },
    disabled: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    }
  })
  
  const emit = defineEmits(['input', 'remove'])
  
  const fileInput = ref(null)
  const downloadingFile = ref(false)
  const isDragging = ref(false)
  let dragCounter = 0
  
  const acceptedFileTypes = computed(() =>
    props.rules.allowedFiles.map(file => file.mime).filter(Boolean).join(',')
  )
  
  const fileList = computed(() => {
    const nadFiles = Array.isArray(props.data?.nad) ? props.data.nad : []
    const customOfferFiles = Array.isArray(props.data?.custom_offer) ? props.data.custom_offer : []
    return [...nadFiles, ...customOfferFiles]
  })
  
  const getFileName = (url) => {
    try {
      return decodeURIComponent(url.split('/').pop())
    } catch {
      return 'File'
    }
  }
  
  function getFileExtension(url) {
    if (!url) {
        return "";
    }
    const parsedUrl = new URL(url);
    const pathname = parsedUrl.pathname;
    const lastDotIndex = pathname.lastIndexOf('.');
  
    if (lastDotIndex === -1) {
        return "";
    }
  
    return pathname.substring(lastDotIndex + 1);
  }
  
  const getMimeFromName = (fileName) => {
    if (!fileName) return ''
    const ext = fileName.split('.').pop().toLowerCase()
    const mimeMap = {
      pdf: 'application/pdf',
      csv: 'text/csv',
      xlsx: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      xls: 'application/vnd.ms-excel'
    }
    return mimeMap[ext] || 'application/octet-stream'
  }
  
  const formatDate = (dateStr) => {
    const date = new Date(dateStr)
    return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`
  }
  
  const getFileIcon = (fileType) => {
    const fileTypeMap = {
      'application/pdf': 'description',
      'text/csv': 'description',
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': 'description',
      'application/vnd.ms-excel': 'description'
    }
    return fileTypeMap[fileType] || 'description'
  }
  
  const formatFileSize = (bytes) => {
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }
  
  const triggerFileInput = () => {
    fileInput.value.click()
  }
  
  const removeFile = (id) => {
    emit('remove', id)
  }
  
  const handleFileChange = (event) => {
    const files = Array.from(event.target.files)
    if (files.length > 0) {
      emit('input', files)
    }
  }
  
  const handleDragEnter = (event) => {
    if (props.disabled) return
    dragCounter++
    isDragging.value = true
  }
  
  const handleDragOver = (event) => {
    if (props.disabled) return
    event.dataTransfer.dropEffect = 'copy'
    isDragging.value = true
  }
  
  const handleDragLeave = (event) => {
    if (props.disabled) return
    dragCounter--
    if (dragCounter === 0) {
      isDragging.value = false
    }
  }
  
  const handleDrop = (event) => {
    if (props.disabled) return
    dragCounter = 0
    isDragging.value = false
    
    const files = Array.from(event.dataTransfer.files)
    if (files.length > 0) {
      emit('input', files)
    }
  }
  
  const downloadFile = async (file) => {
    try {
      if (!file || !file.file) {
        ElMessage({
          message: 'File URL not found',
          type: 'error'
        })
        return
      }
  
      downloadingFile.value = true
      
      const fileUrl = file.file
      
      const link = document.createElement('a')
      link.href = fileUrl
      link.target = '_blank'
      
      const fileName = file.name || getFileName(fileUrl)
      link.download = fileName
      
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      
      ElMessage({
        message: 'File downloaded successfully',
        type: 'success'
      })
    } catch (error) {
      console.error('Download error:', error)
      ElMessage({
        message: 'Failed to download file',
        type: 'error'
      })
    } finally {
      downloadingFile.value = false
    }
  }
  
  defineExpose({
    triggerFileInput,
    triggerInput: triggerFileInput,
    resetUpload: () => {
      if (fileInput.value) {
        fileInput.value.value = null
      }
    },
    downloadFile
  })
  </script>
  
  <style lang="scss" scoped>
  .input-file-preview {
    margin-bottom: 20px;
    width: 100%;
  }
  .input-file.dragging {
    border-color: #409EFF;
    background-color: rgba(64, 158, 255, 0.06);
  }
  
  .file-card {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border: 1px dashed #ccc;
    border-radius: 4px;
  }
  
  .btn.btn-outlined.btn-teal.btn-action {
    border: none !important;
  }
  </style>