<template>
  <Modal v-model="isShow" title="Shipping Information" @close="handleCloseModal">
    <template #default>
      <template v-if="shippingLabelStatus !== '1'">
          <div class="border border-grey-01 rounded-xl p-6 w-full mb-6">
            <div class="flex items-center gap-2 mb-4">
              <i class="material-icons text-red-base icon-md">warning</i>
              <p class="text-2xl font-bold">Regenerate Shiping Label</p>
            </div>
            <p>The shipping label failed to generate. Once the root cause is resolved, you can click "Regenerate Shipping Label" to generate both the shipping label and the shipping kit again. The new tracking number and shipping kit will then be automatically attached to the Offer Details page.</p>
            <Button class="btn btn-teal btn-outlined mt-6" rounded dense @click="regenerateShippingLabel">
              <div class="flex items-center gap-2 px-2 py-1">
                <i class="material-icons text-base">refresh</i>
                <span class="font-medium">Regenerate Shiping Label</span class="text-md">
              </div>
            </Button>
          </div>
        </template>
      <p class="font-bold">Original Tracking Number</p>
      <div>
        <p
          class="mb-2 mt-2 inline-block mr-4"
          :class="{'text-red-base': originalTracking === null || originalTracking === ''}"
        >
          {{ originalTracking || 'Not Available' }}
        </p>
        <Button v-if="!!originalTracking" color="teal" dense @click="copyText(originalTracking || '-')"><i class="material-symbols-outlined">content_copy</i></Button>
      </div>
      <div class="flex-col my-4">
        <p class="font-bold mb-1">Shipping Label Status</p>
          <template
            v-if= "(shippingLabelStatus == 1 )">
            <span class="mr-4 text-grey-04">Available</span>
          </template>
          <template v-else>
            <p class="text-red-base inline-block mr-4">
              Not Available
            </p>
          </template>
        </div>
      <p class="font-bold mt-4">Custom Tracking Number</p>
      <Input v-maska="'X*'" v-model="data.shipping_tracking" @input="handleTrackingChange" block :view="validation.shipping_tracking.$invalid && 'danger' || 'default'" />
      <small class="text-red-base block" v-for="(error, errorIndex) of validation.shipping_tracking.$errors" :key="errorIndex">{{ error.$message }}. </small>
      <Select 
        v-if="showCourierSelect"
        v-model="selectedCourier.courier_name"
        @change="handleSelectCourier"
        :items="courierOptions"
        placeholder="Select one of the available carriers" 
        label="Carrier" 
        class="w-full"
        block
      />
      <small v-if="!selectedCourier.courier_name && showCourierSelect" class="text-red-base mt-4 block">Please select a carrier.</small>
      <div 
        v-if="showWarning"
        class="flex items-start gap-2 text-orange-03 mt-4">
        <span class="material-icons">info</span>
        <p v-if="warningType === 0">The tracking number is either incorrect or not recogniced. You can still save the changes. but the offer status will not automatically updates to 'in-transit'.</p>
      </div>
      <div
        v-if="selectedCourier.courier_name === 'Other'" 
        class="flex items-start gap-2 text-orange-03 mt-4">
        <span class="material-icons">info</span>
        <p>You can still save the changes, but the offer status will not automatically update to 'in-transit'.</p>
      </div>
    </template>
    <template #footer>
      <Button @click="handleSaveChanges" color="teal" :loading="loading">Save Changes</Button>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, reactive, ref, watch, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useCustomTracking, useShowTrackingStatus } from 'composable/buyback'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Select from 'bbUi/components/select/select.vue'
import { ElMessage } from 'element-plus'
import copyText from 'utils/copy'

export default defineComponent({
  components: {
    Input,
    Button,
    Modal,
    Select
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    shipping_number: {
      type: String,
      default: ''
    },
    boxId: {
      type: String,
      default: ''
    },
    originalTracking: {
      type: String,
      default: ''
    },
    shippingLabelStatus:{
      type: String,
      default: ''
    },
    regenerateShippingLabel:{
      type: String,
      default: ''
    }
  },
  emits: ['update:show', 'save'],
  async setup(props, {emit}) {
    const { data, loading, action, actionCheckCourier, unsetData } = useCustomTracking()
    const { action: actionTrackingStatus } = useShowTrackingStatus('online')
    
    const route = useRoute()
    const isShow = ref(props.show)
    const boxId = ref(null)
    const showWarning = ref(false)
    const warningType = ref(0)
    const showCourierSelect = ref(false)
    const isFirstCheck = ref(true)
    const dataCouriers = ref([])

    const selectedCourier = reactive({
      courier_code: '',
      courier_name: ''
    })

    const courierOptions = computed(() => {
      return [...dataCouriers.value.map(courier => courier.courier_name), 'Other']
    })

    const handleSelectCourier = () => {
      data.force_courier_code = 0
      const selectedOption = dataCouriers.value.find(
        courier => courier.courier_name === selectedCourier.courier_name
      )

      if (selectedOption === 'Other') {
        data.courier_code = 'other'
        data.courier_name = 'Other'
      } else if (selectedOption) {
        showWarning.value = false
        data.courier_code = selectedOption.courier_code
        data.courier_name = selectedOption.courier_name
      }
    }

    const handleTrackingChange = () => {
      showWarning.value = false
      showCourierSelect.value = false
      data.force_courier_code = 0
      showCourierSelect.value = false
      isFirstCheck.value = true
    }

    const rules = {
      shipping_tracking: {
        required
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })

    const resetValidation = () => validation.value.$reset()
    
    const validate = () => {
      resetValidation()
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleCloseModal = () => {
      emit('update:show', false)
      showCourierSelect.value = false
      showWarning.value = false
      isFirstCheck.value = true
      unsetData()
      resetValidation()
    }
    const saveCustomTracking = async () => {
      const isValid = validate();
      if (isValid) {
        try {
          await action(boxId.value, () => {
            ElMessage({
              type: 'success',
              message: 'Custom tracking saved',
            });
            emit('update:show', false);
            emit('save', true);
            unsetData();
            resetValidation();
            showCourierSelect.value = false;
            isFirstCheck.value = true;
            selectedCourier.courier_name = '';
            selectedCourier.courier_code = '';
            actionTrackingStatus(route.params.buybackId)
          });
        } catch (error) {
            if (error.response && error.response.status === 422) {
              showWarning.value = true;
              warningType.value = 0;
              data.force_courier_code = 1;
            }
        }
      } 
    };

    const handleSaveChanges = async () => {
      const isValid = validate()
      if (!isValid) return

      if (isFirstCheck.value) {
        isFirstCheck.value = false
        try {
          const response = await actionCheckCourier(data.shipping_tracking)
          
          if (response.code === 4110 || response.code === 4121) {
            showWarning.value = true
            warningType.value = 0
            dataCouriers.value = []
            data.courier_code = null
            data.courier_name = null
          } else if (response.length === 1) {
            data.courier_code = response[0].courier_code
            data.courier_name = response[0].courier_name
            saveCustomTracking()
          } else if (response.length > 1) {
            dataCouriers.value = response
            showCourierSelect.value = true
            showWarning.value = false
          }
        } catch (error) {
          ElMessage({
            type: 'error',
            message: 'Error checking courier service'
          })
        }
      } else if (selectedCourier.courier_name === 'Other') {
        data.courier_code = 'other'
        data.courier_name = 'Other'
        saveCustomTracking()
      } else {
        if(!selectedCourier.courier_name && showCourierSelect.value) return
        saveCustomTracking()
      }
    }
    
    watch(() => props, (propsData) => {
      const isGivenShippingNumber = !!propsData.shipping_number 
      const isEmptyShippingTracking = !data.shipping_tracking
      if(isGivenShippingNumber && isEmptyShippingTracking) data.shipping_tracking = propsData.shipping_number
      boxId.value = propsData.boxId
      isShow.value = propsData.show
    }, {
      deep: true,
      immediate: true
    })

    return {
      copyText,
      isShow,
      data,
      loading,
      validation,
      handleSaveChanges,
      handleCloseModal,
      handleSelectCourier,
      handleTrackingChange,
      selectedCourier,
      courierOptions,
      showWarning,
      warningType,
      showCourierSelect
    }
  }
})
</script>