<template>
    <DocumentFileInput
      ref="documentFileInputRef"
      preview
      :data="uploadedFiles"
      @input="uploadFiles"
      @remove="removeFile"
      :loading="loading.getFiles || loading.uploadFiles || loading.removeFiles"
    >
    
    </DocumentFileInput>
  </template>
  
  <script setup>
  import { computed, onMounted, reactive, ref } from 'vue'
  import DocumentFileInput from 'components/DocumentFileInput.vue'
  import { useEasyOfferDetailStore } from '@/store/pinia/easy-offer/easyOfferDetail'
  import { useRoute } from 'vue-router'
  import { ElMessage } from 'element-plus'
  
  const route = useRoute()
  const documentFileInputRef = ref(null)
  const loading = reactive({
    getFiles: false,
    uploadFiles: false,
    removeFiles: false
  })
  const uploadedFiles = ref([])

  const easyOfferStoreDetail = useEasyOfferDetailStore()

  const getFiles = async () => {
    loading.getFiles = true
    const response = await easyOfferStoreDetail.$getFiles(route.params.buybackId)

    if (response) {
    uploadedFiles.value = response
    }

    loading.getFiles = false
  }

  onMounted(async () => {
    await getFiles()
  })

  
  const uploadFiles = async (files) => {
  if (!files) return

  uploadedFiles.value = []

  const formData = new FormData()

  for (let i = 0; i < files.length; i++) {
    const file = files[i]

    uploadedFiles.value.push({
      name: file.name,
      type: file.type,
      size: file.size,
      file: file
    })

    formData.append('files[]', file)
  }

  try {
    loading.uploadFiles = true
    const response = await easyOfferStoreDetail.$uploadFiles(route.params.buybackId, formData)
    if (response == 'saved'){
      ElMessage({
        message: 'File uploaded!',
        type: 'success'
      })
      await getFiles()
    }
    } catch (err) {
        console.error('Upload failed:', err)
    } finally {
        loading.uploadFiles = false
    }
    }

    const removeFile = async (id) => {
    loading.removeFiles = true
    await easyOfferStoreDetail.$deleteFiles(id)
    await getFiles()
    loading.removeFiles = false
    }
  </script>
  