<template>
  <Card v-if="isShowPictures" class="mt-6 mb-6">
    <template #header>
      <h6>Attachment</h6>
    </template>
    <template #default>
      <Tab view="smooth" :items="tabOption" by="id" name="name" v-model="selectedTab" component>
        <template #title="props">
        <div class="flex flex-row items-center justify-center">
          <span class="block">{{props.item.name}}</span>
          <div class="py-1 px-2.5 ml-4 bg-teal-04 text-white rounded-full flex items-center justify-center box-border text-sm">
            {{ props.item.id === 'images' ? imagesCount : filesCount }}
          </div>
        </div>
      </template>
      </Tab>
      <div v-if="!!statePictures && stateShow?.deleted_at == null && selectedTab === 'files'" v-loading="showPictureLoading.show || showPictureLoading.create || showPictureLoading.delete">
        <Files />
      </div>
      <div v-if="!!statePictures && stateShow?.deleted_at == null && selectedTab === 'images'" v-loading="showPictureLoading.show || showPictureLoading.create || showPictureLoading.delete">
        <MultipleInputFile 
          ref="multipleInputFileRef"
          :disabled="isReadonly" 
          preview 
          @input="uploadFiles"
        >
          <template #preview>
            <div class="w-full flex flex-wrap justify-center">
              <div class="m-4 relative transition duration-200 transform hover:scale-105 cursor-pointer" v-for="(file, fileIndex) of [...statePictures.nad, ...statePictures.custom_offer]" :key="fileIndex">
                <div v-if="route.params.status != 'unlinked'" @click="showPictureDelete(file.id)" class="absolute top-0 right-0 bg-teal-04 w-1 h-1 p-4 box-content flex items-center justify-center transform -translate-x-2 -translate-y-1/2 rounded-full font-semibold text-white hover:bg-white hover:text-teal-04 transition duration-200 cursor-pointer">
                  <i class="material-icons text-lg">close</i>
                </div>
                <img @click="handleZoomImage(file.picture)" class="rounded-md" :src="file.picture" :alt="`picture-${fileIndex}`">
              </div>
            </div>
          </template>
          <template #default>
            <div class="flex flex-col justify-center items-center text-center">
              <span class="material-symbols-outlined icon-xl">upload</span>
              <strong class="text-xl">Choose files or drag them here</strong>
              <p class="text-base text-grey-02 font-light">Upload multiple PNG, JPG, or JPEG images</p>
              <Button class="mt-4" color="teal" view="outlined" @click.prevent="triggerFileInput">Upload Files</Button>
            </div>
          </template>
        </MultipleInputFile>
      </div>
    </template>
  </Card>
</template>

<script>
import { defineComponent, onUnmounted, watch, computed, ref } from 'vue'
import { useShow, useShowPictures } from 'composable/buyback'
import { useRoute } from 'vue-router'
import Card from 'bbUi/components/card/card.vue'
import MultipleInputFile from 'components/MultipleInputFile.vue'
import { ElMessage } from 'element-plus'
import debounce from 'utils/debounce'
import { useBulkQuote } from 'composable/bulkquote';
import Button from 'bbUi/components/button/button.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import DocumentFileInput from 'components/DocumentFileInput.vue'
import Files from './Files.vue'
import { useEasyOfferDetailStore } from '../../../store/pinia/easy-offer/easyOfferDetail'

export default defineComponent({
  components: {
    Card,
    MultipleInputFile,
    DocumentFileInput,
    Button,
    Tab,
    Files
  },
  props: {
  },
  async setup(props) {
    const multipleInputFileRef = ref(null)
    const { isBulkQuote } = useBulkQuote()
    const route = useRoute()
    const { state: stateShow, action: actionShow } = useShow('online')
    const { loading: showPictureLoading, state: statePictures, actionShow: actionShowPictures, actionCreate: actionShowPictureCreate, actionDelete: actionShowPictureDelete } = useShowPictures('online')
    const isReadonly = computed(() => stateShow.value && stateShow.value.is_readonly)
    const isShowPictures = computed(() => !isBulkQuote.value)

    const easyOfferDetailStore = useEasyOfferDetailStore()

    const filesCount = computed(() => {
      const nad = easyOfferDetailStore?.fileUpload?.nad ?? []
      const custom = easyOfferDetailStore?.fileUpload?.custom_offer ?? []
      return [...nad, ...custom].length
    })

    const imagesCount = computed(() => {
      const nad = statePictures.value?.nad ?? []
      const custom = statePictures.value?.custom_offer ?? []
      return [...nad, ...custom].length
    })

    const selectedTab = ref('files')

    const tabOption = [
      {
        id: 'files',
        name: 'Files',
      },
      {
        id: 'images',
        name: 'Images',
      }
    ]
    
    const uploadFiles = async (files) => {
      const data = new FormData()
      for( let [index, file] of Object.entries(files) ){
        data.append(`picture_files[${index}]`, file)
      }
      await actionShowPictureCreate(route.params.buybackId, data, async() => {
        await actionShowPictures(route.params.buybackId)
        ElMessage({
          message: 'Picture uploaded!',
          type: 'success'
        })
      })
    }
    
    const showPictureDelete = async (fileId) => {
      await actionShowPictureDelete(fileId, async() => {
        await actionShowPictures(route.params.buybackId)
        ElMessage({
          message: 'Picture deleted!',
          type: 'success'
        })
      })
    }
    
    const handleZoomImage = (url) => {
      if(!!url) window.open(url, '_blank')
    }
    
    const triggerFileInput = () => {
      multipleInputFileRef.value.triggerFileInput()
    }
    
    await actionShowPictures(route.params.buybackId)

    return {
      multipleInputFileRef,
      isBulkQuote,
      route,
      stateShow,
      isReadonly,
      isShowPictures,
      statePictures,
      showPictureLoading,
      showPictureDelete,
      uploadFiles,
      handleZoomImage,
      triggerFileInput,
      selectedTab,
      tabOption,
      filesCount,
      imagesCount
    }
  }
})
</script>

<style lang="scss" scoped>
  .upload-icon {
    font-size: 1.5rem !important;

  }
</style>