import { defineStore } from 'pinia'
import request from 'utils/request'

export const useEasyOfferDetailStore = defineStore('easyOfferDetail', {
    state: () => ({
        easyOfferDetail: null,
        fileUpload: null,
        loading: {
            getDetail: false,
            getFiles: false,
            uploadFiles: false,
            deleteFiles: false,
        }
    }),
    getters: {},
    actions: {
        async $actionShow(buybackId) {
            try {
                this.$state.loading.getDetail = true
                const response = await request({
                    url: `/v1/pos/easy-offer/show/${buybackId}`,
                    method: 'get'
                })

                this.$state.easyOfferDetail = response
                return response
            } catch (error) {
                console.error(error)
            } finally {
                this.$state.loading.getDetail = false
            }
        },
        async $getFiles(buybackId) {
            try {
                this.$state.loading.getFiles = true
                const response = await request({
                    url: `/v1/pos/easy-offer/file/${buybackId}`,
                    method: 'get'
                })

                this.$state.fileUpload = response
                return response
            } catch (error) {
                console.error(error)
            } finally {
                this.$state.loading.getFiles = false
            }
        },
        async $uploadFiles(buybackId, payload) {
            try {
                this.$state.loading.uploadFiles = true
                const response = await request({
                    url: `/v1/pos/easy-offer/file/${buybackId}`,
                    method: 'post',
                    data: payload
                })

                return response
            } catch (error) {
                console.error(error)
            } finally {
                this.$state.loading.uploadFiles = false
            }
        },
        async $deleteFiles(fileId) {
            try {
                this.$state.loading.deleteFiles = true
                const response = request({
                    url: `/v1/pos/easy-offer/file/${fileId}`,
                    method: 'delete',
                })
                return response
            } catch (error) {
                console.error(error)
            } finally {
                this.$state.loading.deleteFiles = false
            }
        }
    }
})