    }
    const updateDevice = async () => {
      const isValid = await nextTick(() => refDeviceForm.value.validate())
      if( isValid ){
        const deviceData = {
        ...device.data
        }
        delete deviceData.color
        Object.assign(deviceData, {
          product_id: device.data.product_id ? device.data.product_id + (device.computer.processor ? `-${device.computer.processor.id}-${device.computer.memory.id}-${device.computer.hardDrive.id}-${device.computer.graphicsCard.id}` : '') : -1,
