      const isValid = await nextTick(() => refDeviceForm.value.validate())
      if(isValid) {
        const newData = {
          ...(device.data),
          ...(transformSeperateCustomSpecs(device.golfSpecs)),
          ...(device.data.is_custom_color
            ? {
                custom_color: device.data.custom_color,
                color_id: null
              }
            : {
                color_id: device.data.color_id
              })
        }
        delete newData.color
        device.data = Object.assign(newData)
        await actionCreate(route.params.buybackId)
      }
    }
    const items = computed(() => {
      return JSON.parse(JSON.stringify(state.value))
    })
    const tableSummary = ({columns}) => {
      return columns.map(column => column.property === 'total' ? `${currency(me.value, state.value.total)}` : '' || column.property === 'price' ? 'Total' : '')
    }
    const selectItems = (items) => {
      selectedItems.value = items;
      validPhonecheckselectedItems.value = items.filter((item) => item.phonecheck_status == 'exists');
      checkSameProduct();
    }

    const checkSameProduct = () => {
      sameProduct.value = false;
      if (selectedItems.value.length > 0) {
        sameProduct.value = true;
        const isMac = selectedItems.value[0].is_mac;
        const categoryId = selectedItems.value[0].category_id;

        selectedItems.value.forEach(item => {
          if (item.is_mac != isMac) sameProduct.value = false;
          if (item.category_id != categoryId) sameProduct.value = false;
        });
      }
    }

    const splitItems = async () => {
      const isSelectedItems = selectedItems.value.length > 0
      const isSelectedStatus = dataSplitItems.new_status
      if( !isSelectedItems ) return ElMessage({
        message: 'Please choose at least one item',
        type: 'warning'
      })
      if( !isSelectedStatus ) return ElMessage({
        message: 'Please choose new status',
        type: 'warning'
      })
      const itemIds = selectedItems.value.map(item => item.id)
      Object.assign(dataSplitItems, {
        ids: itemIds
      })

      await actionSplitItems(route.params.buybackId, async () => {
        selectedItems.value = []
        ElMessage({
          message: 'Order splitted!',
          type: 'success'
        })
        await Promise.all([
          actionAll(route.params.buybackId),
          actionShow(route.params.buybackId),
          actionAllStatuses()
        ])
      })
    }
    const tabValidate = (tab) => {
      return tab.id !== 5
    }
    const openDataMatch = () => router.push({
      name: 'easyOfferDataMatch',
      params: {
        buybackId: route.params.buybackId
      }
    })
