    //     }
    //     // await updateShippingInsurance(insurance_fee);
    //   }
    //   ShippingInsurance.value.validation = validation.value;
    // }
    // const updateShippingInsurance = async (insurance_fee) => {
    //   const payload = {
    //     insurance_fee
    //   }
    //   await actionUpdateShippingInsurance(route.params.buybackId, payload, async () => {
    //     await actionAll(route.params.buybackId)
    //   })
    // }
    const handleOpenAddBulkImeiModal = (item) => {
      const { product_id, product_name, condition, price, qty } = item
      dataBulkImeiAddDevice.product_id = product_id,
      dataBulkImeiAddDevice.product_name = product_name
      dataBulkImeiAddDevice.condition = condition
      dataBulkImeiAddDevice.price = price
      dataBulkImeiAddDevice.qty = qty
      const result = []
      item?.child?.length && item.child.forEach(child => {
        result.push(child?.imei_sn || '')
      })
      dataBulkImeiAddDevice.imei = result

      isBulkImei.value = true
      modalBulkImeiAddDevice.value = true
    }
    const handleCloseAddBulkImeiModal = () => {
      isBulkImei.value = false
      modalBulkImeiAddDevice.value = false
      refBulkImeiSNInput?.value?.validate.$reset()
      unsetDataBulkImeiAddDevice()
    }
    const handleAddBulkImeiDevice = async () => {
      if (!await refBulkImeiSNInput.value.validate.$validate()) return

      await actionBulkImeiAddDevice(route.params.buybackId, async () => {
        handleCloseAddBulkImeiModal()
        await Promise.all([
          actionAll(route.params.buybackId),
          actionShow(route.params.buybackId),
          actionAllStatuses()
        ])
        ElMessage({
          message: 'Items added',
          type: 'success'
        })
      })
    }

    const bulkCondition = reactive({
      modal: false,
      loading: false
    })

    const handleBulkChangeCondition = async () => {
      bulkCondition.modal = true;

      const isCustomProduct = selectedItems.value.length > 0 ? selectedItems.value[0].is_custom == 1 : false
      if(isCustomProduct) {
        await actionCustomConditions({
          category_id: selectedItems.value[0].category_id,
          is_mac: selectedItems.value[0].is_mac
        })
      }else {
        await actionCondition({
          product_id: selectedItems.value[0].product_id,
          is_mac: selectedItems.value[0].is_mac,
        })
      }
    }

    const handleCloseModalBulkCondition = () => {
      bulkCondition.modal = false;
    }

    const handleBulkChangeItemCondition = async (data) => {
      await updateBulkCondition({
        boxId: route.params.buybackId,
        data: data,
      })

      ElMessage({
        type: 'success',
        message: 'Item conditions updated'
      })
      bulkCondition.modal = false;
      selectedItems.value = []
      refItemTable.value.checkValue = []

      await Promise.all([actionAll(route.params.buybackId), actionAllStatuses()])
    }
    const handleApplyCoupon = async () => {
      await applyCoupon({ boxId: route.params.buybackId }, async (err, response) => {
        if (err) return
        await actionAll(route.params.buybackId)
      })
    }
    const handleRemoveCoupon = async () => {
      await removeCoupon({boxId: route.params.buybackId}, async (err, response) => {
        if(err) return
        await actionAll(route.params.buybackId)
      })
    }

    // action's must be called after lifecycle hook declaration
    await actionEnabledIntegration()
    await Promise.all([actionAll(route.params.buybackId), actionAllStatuses()])

    return {
      me,
      device,
      state,
      stateStatuses,
      stateShow,
      items,
      selectedItems,
      dataSplitItems,
      loading,
      loadingSplitItems,
      modal,
      modalSplitItems,
      printLabelData,
      unsetData,
      unsetDataSplitItems,
      dataBulkImeiAddDevice,
      dataCoupon,
      loadingBulkImeiAddDevice,
      loadingCoupon,
      modalBulkImeiAddDevice,
      isBulkImei,
      bulkImei,
      splitItems,
      openSplitItemsModal,
      rowKey,
      isParent,
      refDeviceForm,
      refPhonecheckInfo,
      refBulkImeiSNInput,
      openModalSelected,
      deviceTableUpdate,
      addDevice,
      deleteDevice,
      updateDevice,
      money,
      openModal,
      tableSummary,
      selectItems,
      tabValidate,
      openDataMatch,
      printQrLabel,
      printBarcodeLabel,
      printQrLabelBatch,
      printBarcodeLabelBatch,
      viewPhoneCheckData,
      importPhoneCheck,
      PhonecheckInfoData,
      PhonecheckDevice,
      isErrorSelectedPhonecheck,
      printPhonecheck,
      imeiUpdate,
      validPhonecheckselectedItems,
      importBulkPhoneCheck,
      isReadonly,
      isCouponApplied,
      isCouponChanged,
      hasEnablePhonecheck,
      actionEnabledIntegration,
      importSinglePhoneCheck,
      // ShippingInsurance,
      // editShippingInsurance,
      // deleteShippingInsurance,
      // cancelShippingInsurance,
      // saveShippingInsurance,
      handleOpenAddBulkImeiModal,
      handleCloseAddBulkImeiModal,
      handleAddBulkImeiDevice,
      handleBulkChangeCondition,
      bulkCondition,
      handleCloseModalBulkCondition,
      handleBulkChangeItemCondition,
      handleApplyCoupon,
      handleRemoveCoupon,
      refItemTable,
      sameProduct,
      loadingCondition,
      itemConditions,
      labelType,
      isExistAllPhonecheck,
      sendEmailPhonecheck,
      isShowCommission
    }
  }
})
</script>
