        </p>
      </template>
      <template v-else-if="stateShow.billing?.payment_method === 'Bill'">
        <p class="flex items-center gap-2">
          <strong class="text-sm">Legal Business Name</strong>
          {{ stateShow.billing?.bill_legal_business_name }}
          <i
            class="material-icons ml-4 text-teal-04 hover:text-teal-03 cursor-pointer"
            @click="CopyText(stateShow.billing?.bill_legal_business_name)">
