          </i>
        </p>
        <p class="flex items-center gap-2">
          <strong class="text-sm">Network ID</strong>
          {{ stateShow.billing?.bill_network_id }}
          <i
            class="material-icons ml-4 text-teal-04 hover:text-teal-03 cursor-pointer"
            @click="CopyText(stateShow.billing?.bill_network_id)">
            file_copy
          </i>
        </p>
      </template>
      <template v-else-if="stateShow.billing?.payment_method === 'Rebate'">
        <p class="flex items-center gap-2">
