        </div>
        <template v-if="isAbandonedBuyback && !isReadonly">
          <Button
            :disabled="stateShow?.deleted_at != null || bulkQuote.restrictedQuoteUpdate"
            :loading="loadingPaymentMethod.all"
            color="teal"
            @click="handleOpenPayoutMethodModal"
            dense
            ><i class="material-icons text-base">edit</i> Edit</Button
          >
        </template>
        <template v-else-if="!isReadonly">
          <Button
            :disabled="stateShow?.deleted_at != null || bulkQuote.restrictedQuoteUpdate"
            :loading="loadingPaymentMethod.all"
            color="teal"
            @click="handleOpenPayoutMethodModal"
            dense
            ><i class="material-icons text-base">edit</i> Edit</Button
