  <!-- <Modal v-model="modal.customer" title="Change Customer">
    <p>asd</p>
  </Modal> -->
  <Modal
    v-model="modal.paymentMethod"
    :title="isBulkQuote ? 'Update Payout' : 'Update Offer Information'"
    :hide-footer="errorsPaymentMethod.all"
