    <template v-if="errorsPaymentMethod.all">
      <div class="mb-4 flex flex-col items-center justify-center">
        <p>There's an error when getting payout info, please try again</p>
        <Button
          class="mt-4"
          color="teal"
          @click="() => $router.go()"
          ><i class="material-icons text-lg">refresh</i> Reload Page</Button
        >
      </div>
    </template>
    <template v-else>
      <Tab v-model="tab.selected" :items="tabItems" component />
      <component
        ref="refComponent"
        v-bind="componentBind"
        v-on="componentOn"
        :is="tab.selected"
      ></component>
    </template>
    <template #footer>
      <Button
        color="teal"
        :loading="loadingPaymentMethod.update"
        @click="updatePaymentMethod()"
        >{{ isBulkQuote ? 'Save Payout' : 'Save Offer Information' }}</Button
      >
    </template>
  </Modal>
  <Modal v-model="modalAbandonBuyback.update.customer" title="Edit Customer">
    <template #default>
      <AbandonBuybackCustomerForm
        ref="refAbandonBuybackCustomerForm"
        v-model:data="dataAbandonBubyback.customer"
      />
    </template>
    <template #footer>
      <div class="flex justify-end">
        <Button
          @click="handleUpdateAbandonBuybackCustomer"
          :loading="loadingAbandonBuyback.update.customer"
          color="teal"
          >Save</Button
        >
      </div>
    </template>
  </Modal>
  <CustomTracking
    v-model:show="modalCustomTracking"
    :box-id="stateShow.box_id"
    :original-tracking="stateShow.original_tracking"
    @save="handleCustomTrackingSaved"
  />
</template>

<script>
import {
  defineComponent,
  computed,
  ref,
  reactive,
  nextTick,
  watch,
  onUnmounted
} from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useShow as useShowCustomer } from 'composable/customer'
import {
  useShow,
  useShowItems,
  useShowPaymentMethod,
  usePrint,
  useShowTrackingStatus,
  useShowPrintStatus,
  useCustomTracking,
  useAbandonBuyback,
  useRegenerateShipping
} from 'composable/buyback'
import { useUser } from 'composable/user'
import { useBuybackShipping } from 'store/pinia/buyback/shipping'
import { tryCatchRequest } from '@/utils/request'
import { anchorPrint } from 'utils/element'
import { kebabCase, upperCase } from 'utils/string'
import { sellingTypeColor } from 'utils/status'
import { color } from 'utils/status'
import debounce from 'utils/debounce'
import { ElMessage } from 'element-plus'
import { printButtonTransfer } from 'utils/dymo'
import CopyText from 'utils/copy'
import { nameCapitalize } from 'utils/element'
import Alert from 'bbUi/components/alert/alert.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import Input from 'bbUi/components/input/input.vue'
import Loading from 'bbUi/components/loading/loading.vue'
import PaymentMethod from 'components/PaymentMethod.vue'
import ShippingLabelInfo from 'components/Buyback/ShippingLabelInfoForm.vue'
import { phoneNumber } from 'utils/number'
import TextLimit from 'components/TextLimit.vue'
import CustomTracking from './CustomTracking.vue'
import AbandonBuybackCustomerForm from 'components/Buyback/AbandonCustomerForm.vue'
import Address from 'components/Address/index.vue'
import Currency from "components/Currency.vue";
import ExpeditedIcon from 'components/ExpeditedPayout/ExpeditedIcon.vue'
import DetailContactOrderInformation from 'components/Buyback/DetailContactOrderInformation.vue'
import DetailCustomerPortalLink from 'components/Buyback/DetailCustomerPortalLink.vue'
import BuybackShippingOptions from 'components/Features/Buyback/BuybackShipping/BuybackShippingOptions.vue'

export default defineComponent({
  components: {
		Currency,
    Alert,
    Badge,
    Button,
    Card,
    Modal,
    Tab,
    Input,
    Loading,
    PaymentMethod,
    ShippingLabelInfo,
    TextLimit,
    CustomTracking,
    AbandonBuybackCustomerForm,
    Address,
    ExpeditedIcon,
    DetailContactOrderInformation,
    DetailCustomerPortalLink,
    BuybackShippingOptions
  },
  props: {
    bulkQuote: {
      type: Object,
      default: () => ({
        restrictedQuoteUpdate: false,
        restrictedQuoteItemUpdate: false,
        restrictedQuoteStatusUpdate: false,
        restrictedQuotePercentageUpdate: false,
        isReconcilable: false,
        totalQuoteItems: 0
      })
    },
    isBulkQuote: {
      type: Boolean,
      default: false
    }
  },
  async setup(props) {
    const {
      state: stateShow,
      modal,
      openModal,
      loading: loadingShowOffer,
      action: actionShow,
      stateOrdersToReloads,
      resetStateOrdersToReloads
    } = useShow('online')
    
    const { state: stateItems } = useShowItems('online')
    const {
      state: statePaymentMethod,
      data: dataPayment,
      loading: loadingPaymentMethod,
      errors: errorsPaymentMethod,
      showBankNumber,
      actionAll: actionPaymentMethods,
      actionUpdate: actionPaymentMethodUpdate,
      actionShowBankNumber,
      actionShowCheckSettings
    } = useShowPaymentMethod('online')
    const { loading: loadingPrint, actionPaperCheck } = usePrint()
    const {
      state: stateTrackingStatus,
      loading: loadingTrackingStatus,
      action: actionTrackingStatus
    } = useShowTrackingStatus('online')
    const { loading: loadingPrintStatus, action: actionPrintStatus } =
      useShowPrintStatus('online')
    const { modal: modalCustomTracking, openModal: openModalCustomTracking } =
      useCustomTracking()
    const { bankTransferConfig, me } = useUser()
    const { state: stateCustomer, action: actionCustomer } = useShowCustomer()
    const {
      data: dataAbandonBubyback,
      modal: modalAbandonBuyback,
      updateCustomer: updateAbandonBuybackCustomer,
      loading: loadingAbandonBuyback
    } = useAbandonBuyback('online')
     const {
      loading: loadingRegenerateShipping,
      regenerateShipping
    } = useRegenerateShipping('online') 
    const tab = reactive({
      selected: 'PaymentMethod',
    })
    const tabItems = computed(() => {
      const items =  [
        {
          name: props.isBulkQuote ? 'Shipping Label Info' : 'Offer Details',
          component: 'ShippingLabelInfo'
        },
        {
          name: props.isBulkQuote ? 'Payment Method' : 'Offer Payout Method',
          component: 'PaymentMethod'
        },
      ]
      if(props.isBulkQuote && stateShow.value?.status_id !== 9000) {
        return [{
          name: 'Payment Method',
          component: 'PaymentMethod'
        }]
      }
      return items
    })

    const store = useBuybackShipping()
    const route = useRoute()
    const router = useRouter()
    const refComponent = ref(null)
    const refAbandonBuybackCustomerForm = ref(null)
    const isShowBankNumber = ref(false)
    const dataPaymentMethod = ref({})
    const initialShippingLabelData = reactive({})
    const isReadyPaperCheck = ref(false)
    const loadingShippingInfo = ref(false)

    const componentBind = computed(() => {
      if (tab.selected === 'PaymentMethod') {
        return {
          autoSelect: true,
          items: statePaymentMethod.value,
          data: stateShow.value.billing
        }
      }
      if (tab.selected === 'ShippingLabelInfo') {
        return {
          data: Object.assign(dataPayment.shippingLabelInfo, {
            ...stateShow.value.shipping,
            company_name: stateShow?.value.billing?.company_name,
            custom_fields: customFieldsFormatted.value,
          }),
          isEasyOffer: isEasyOffer.value,
        }
      }
    })

    const componentOn = computed(() => {
      if (tab.selected === 'PaymentMethod') {
        return Object.assign(
          {},
          {
            'update:data': (paymentMethod) =>
              (dataPaymentMethod.value = paymentMethod)
          }
        )
      }
      return {}
    })
    const customFields = computed(() => {
      const customFields = stateShow.value.custom_fields
      const isEmptyCustomFields = !customFields
      if(isEmptyCustomFields) return []
      return Object.entries(customFields).map(([_, field]) => ({label: field.key, value: field.value}))
    })

    const customFieldsFormatted = computed(() => {
      return Object.fromEntries(
        customFields.value.map(field => [field.label, field.value])
      );
    })

    const isEasyOffer = computed(() => {
      return stateShow?.value.type === 3 && stateShow?.value.type_text === "Easy Offer"
    })

    const setInitialShippingLabelData = () => {
      Object.assign(initialShippingLabelData, {
        ...dataPayment.shippingLabelInfo,
        ...stateShow.value.shipping
      })
    }
    const paperCheck = () => actionPaperCheck(route.params.buybackId)
    const updatePaymentMethod = async () => {
      // Join shipping label info data with payment methods data
      Object.assign(dataPaymentMethod.value, {
        ...stateShow.value.shipping,
        ...dataPayment.shippingLabelInfo
      })

      const isUpdatedShippingLabelInfoData =
        Object.entries(dataPaymentMethod.value).filter(([key, value]) => {
          if (Object.keys(initialShippingLabelData).includes(key)) {
            return initialShippingLabelData[key] !== value
          }
          return false
        }).length > 0
      const isGenerateNewShipping = isUpdatedShippingLabelInfoData
      const isValid = await nextTick(() =>
        tab.selected === 'PaymentMethod'
          ? refComponent.value.refChildComponent.validate()
          : refComponent.value.validate()
      )
      if (isValid) {
        actionPaymentMethodUpdate(
          route.params.buybackId,
          {
            ...dataPaymentMethod.value,
            is_generate_shipping: isGenerateNewShipping ? 1 : 0
          },
          async () => {
            modal.paymentMethod = false
            await actionShow(
              {
                id: route.params.buybackId,
                is_store: 1
              },
              () => {
                setInitialShippingLabelData()
              }
            )
          }
        )
      }
    }
    const toggleShowBankNumber = () => {
      isShowBankNumber.value = !isShowBankNumber.value
      // if( !showBankNumber.value ) await actionShowBankNumber(route.params.buybackId)
      // isShowBankNumber.value = !isShowBankNumber.value
    }
    const changeStatusPrint = () => {
      actionPrintStatus(route.params.buybackId, async () => {
        ElMessage({
          message: 'Print status updated',
          type: 'success'
        })
        await actionShow({
          id: route.params.buybackId,
          is_store: 1
        })
      })
    }
    const printAddressLabel = () => {
      const addressComponent = `${
        stateShow.value.shipping?.shipping_suite
          ? upperCase(stateShow.value.shipping?.shipping_suite) + ', '
          : ''
      }${upperCase(stateShow.value.shipping.shipping_address1) || ''}\n${
        upperCase(stateShow.value.shipping.shipping_city) || ''
      } ${upperCase(stateShow.value.shipping.shipping_state) || ''} ${
        upperCase(stateShow.value.shipping.shipping_zip) || ''
      }`
      const fullname = `${stateShow.value.shipping.shipping_firstname || ''} ${
        stateShow.value.shipping.shipping_lastname || ''
      }`
      const imb = stateShow.value.shipping.shipping_imb || ''
      printButtonTransfer(addressComponent, fullname, imb)
    }

    const handleCustomTrackingSaved = async () =>
      await actionShow(
        {
          id: route.params.buybackId,
          is_store: 1
        },
        async (data) => {
          await actionPaymentMethods({
            id: data.customer_id,
            params: {
              box_id: route.params.buybackId
            }
          })
        }
      )

    const isLoading = computed(() => {
      return Object.keys(stateShow.value).length === 0
    })
    const isAbandonedBuyback = computed(
      () => stateShow.value?.status_id == 1000
    )
    const isNotAbandonedBuyback = computed(() => !isAbandonedBuyback.value)
    const isReadonly = computed(() => stateShow.value && stateShow.value.is_readonly)

    // const stopWatcher = watch(
    //   () => route.params.buybackId,
    //   debounce(async (newBuybackId, oldBuybacKId) => {
    //     if (newBuybackId && oldBuybacKId) {
    //       isShowBankNumber.value = false
    //       showBankNumber.value = null
    //       await actionTrackingStatus({
    //         id: newBuybackId
    //       })
    //       await actionShow({
    //         id: route.params.buybackId
    //       }, async (data) => {
    //         await actionPaymentMethods({
    //           id: data.customer_id,
    //           params: {
    //             box_id: route.params.buybackId
    //           }
    //         })
    //       })
    //     }
    //   }, 500)
    // )
    // // IDK watcher is not destroyed when component is unmounted, it needs to call stopWatcher to manually destroy the watcher
    // onUnmounted(() => stopWatcher())

    await actionTrackingStatus(route.params.buybackId)
    await actionShow(
      {
        id: route.params.buybackId,
        is_store: 1
      },
      async (data) => {
        await actionPaymentMethods({
          id: data.customer_id,
          params: {
            box_id: route.params.buybackId
          }
        })
        await actionCustomer(data.customer_id)
        setInitialShippingLabelData()
      }
    )
    await actionShowCheckSettings((checkSettings) => {
      isReadyPaperCheck.value =
        checkSettings.is_ready_in_store == 1 &&
        checkSettings.is_enable_check == 1
    })

    if (stateShow.value.buyback_type === 'Local Buyback')
      router.push({
        name: 'buybackLocalDetail',
        params: {
          buybackId: route.params.buybackId
        }
      })

    const handleLoginAsCustomer = (link = null) => {
      if (!link) return null
      window.open(link, '_blank').focus()
    }
    const handleOpenShippingLabelInfoModal = () => {
      tab.selected = 'ShippingLabelInfo'
      if (stateShow.value.shipping) {
        Object.keys(stateShow.value.shipping).forEach((item) => {
          if (!stateShow.value.shipping[item]) {
            stateShow.value.shipping[item] =
              stateShow.value.customer[item.replace('shipping_', '')]
          }
        })
      }
      openModal('paymentMethod')
    }
    const handleOpenPayoutMethodModal = () => {
      tab.selected = 'PaymentMethod'
      openModal('paymentMethod')
    }
    const handleUpdateAbandonBuybackCustomer = async () => {
      if (!refAbandonBuybackCustomerForm.value) return
      const isValid = refAbandonBuybackCustomerForm.value.validate()
      if (isValid) {
        await updateAbandonBuybackCustomer(
          stateShow.value.customer.id,
          async () => {
            modalAbandonBuyback.update.customer = false
            ElMessage({
              type: 'success',
              message: 'Customer information saved'
            })
            await actionShow({
              id: route.params.buybackId,
              is_store: 1
            })
            await actionPaymentMethods({
              id: stateShow.value.customer.id,
              params: {
                box_id: route.params.buybackId
              }
            })
          }
        )
      }
    }
    const handleOpenAbandonBuybackCustomerModal = () => {
      modalAbandonBuyback.update.customer = true
      Object.assign(dataAbandonBubyback.customer, {
        box_id: stateShow.value.box_id,
        email: stateShow.value.customer.email,
        firstname: stateShow.value.customer.first_name,
        lastname: stateShow.value.customer.last_name,
        phone: stateShow.value.customer.phone,
        address: stateShow.value.customer.address1,
        suite: stateShow.value.customer.suite,
        city: stateShow.value.customer.city,
        state: stateShow.value.customer.state,
        zip: stateShow.value.customer.zip,
        country: stateShow.value.customer.country,
        address_text: stateShow.value.customer.address_text
      })
    }
    const confidentialView = computed(() => {
      return (
        me.value.is_view_confidential &&
        stateShow.value.billing?.payment_method === 'Bank Transfer'
      )
    })
    const computedBankTransfer = computed(() => {
      const info = []
      if (
        stateShow.value.billing?.bank_transfer.length &&
        !isShowBankNumber.value
      ) {
        stateShow.value.billing?.bank_transfer.forEach((item) => {
          const tempItem = { ...item }
          if (tempItem.attribute_name !== 'account_name') {
            if(tempItem.value){
              const encryptor = tempItem.value
                .split('')
                .splice(0, tempItem.value.length - 3)
                .map(() => '*')
              const codes = tempItem.value
                .split('')
                .splice(tempItem.value.length - 3, 3)
              tempItem.value = `${encryptor.join('')}${codes.join('')}`
            }else{
              tempItem.value = '-'
            }
          }
          info.push(tempItem)
        })
        return info
      }
      return stateShow.value.billing?.bank_transfer
    })
    const computedCustomer = computed(() => {
      const { shipping, customer } = stateShow.value || {}
      return ({
        firstName: (shipping && shipping?.shipping_firstname) || customer?.first_name || '-',
        lastName: (shipping && shipping?.shipping_lastname) || customer?.last_name || '-',
        email: (shipping && shipping?.email) || customer?.email || '-',
        phone: (shipping && shipping?.shipping_phone) || customer?.phone || '-',
        addressCustomer:
          customer?.address_text ?
            customer?.suite ? `${customer?.suite}, ${customer?.address_text}`
              : customer?.address_text
            : '-'
      })
    })
    const isShowTrackingNumber = computed(() => {
      if (loadingShowOffer.value) return false
      if(props.isBulkQuote) {
        const bulkQuoteStatusQuote = '9000'
        const bulkQuoteStatusPendingContract = '9001'
        return ![bulkQuoteStatusQuote, bulkQuoteStatusPendingContract].includes(String(stateShow.value?.status_id))
      }
      return true
    })
    const handleSucceedBuybackOption = async () => {
      await actionShow({
        id: route.params.buybackId,
        is_store: 1
      })
    }
    const handleRegenerateShipping = async () => {
      const boxId = stateShow.value.box_id || route.params.buybackId
      if (!boxId) {
        ElMessage({
          type: 'warning',
          message: 'Box ID Undefined'
        })
        return
      }

      const response = await regenerateShipping(boxId)
      
      ElMessage({
        type: 'success',
        message: response?.result || response?.message || 'Success'
      })

      await actionShow({
        id: route.params.buybackId,
        is_store: 1
      })
    }
    const isDisabledPrintShippingKit = computed(
      () =>
        stateShow.value?.shipping_label_is_generate === 0 ||
        stateShow.value?.shipping_label_status == 2 ||
        loadingShowOffer.value === true
    )
    const printShippingKit = async () => {
      if (!route.params.buybackId || isDisabledPrintShippingKit.value) return
      store.shippingKit.modal = true
    }
    const onDownload = async () => {
      await tryCatchRequest(
        store.shippingKit,
        async () => {
          await store.$downloadShippingKit({
            id: stateShow.value?.box_id,
            params: {
              order_shipping_label_id: stateShow.value?.shipping_label?.order_shipping_label
            }
          }).then(res => {
            anchorPrint(res)
          })
        }
      )
    }

    watch(() => stateOrdersToReloads.value, async (value) => {
      if (value?.offer_id && value?.offer_has_changes && (route?.params?.buybackId === value?.offer_id)) {
        await actionShow({
          id: route.params.buybackId,
          is_store: 1
        })
        resetStateOrdersToReloads()
      }
    }, {
      deep: true
    })

    return {
      dataPaymentMethod,
      dataAbandonBubyback,
      tab,
      componentBind,
      componentOn,
      customFields,
      stateShow,
      stateItems,
      statePaymentMethod,
      stateTrackingStatus,
      bankTransferConfig,
      showBankNumber,
      printAddressLabel,
      isShowBankNumber,
      loadingPaymentMethod,
      loadingPrint,
      loadingTrackingStatus,
      loadingPrintStatus,
      loadingAbandonBuyback,
      isLoading,
      isNotAbandonedBuyback,
      isReadonly,
      isAbandonedBuyback,
      isReadyPaperCheck,
      errorsPaymentMethod,
      modal,
      modalCustomTracking,
      modalAbandonBuyback,
      refComponent,
      refAbandonBuybackCustomerForm,
      openModal,
      openModalCustomTracking,
      paperCheck,
      updatePaymentMethod,
      changeStatusPrint,
      color,
      sellingTypeColor,
      kebabCase,
      toggleShowBankNumber,
      handleCustomTrackingSaved,
      CopyText,
      phoneNumber,
      handleLoginAsCustomer,
      handleOpenShippingLabelInfoModal,
      handleOpenPayoutMethodModal,
      handleUpdateAbandonBuybackCustomer,
      handleOpenAbandonBuybackCustomerModal,
      nameCapitalize,
      stateCustomer,
      computedBankTransfer,
      confidentialView,
      computedCustomer,
      isShowTrackingNumber,
      tabItems,
      actionShow,
      handleSucceedBuybackOption,
      loadingShippingInfo,
      handleRegenerateShipping,
      loadingRegenerateShipping,
      printShippingKit,
      onDownload,
      store
    }
  }
})
</script>

<style lang="scss" scoped>
.icon-visibility {
  vertical-align: middle !important;
  font-size: 1.5rem !important;
}
</style>
