<template>
  <Input
    v-if="data.status_id === 6"
    v-model="data.returned_tracking"
    type="input"
    label="Returned Tracking"
    class="w-full mt-6"
    :disabled="disabled"
  />
  <!-- <Input v-model="data.notes" type="textarea" label="Notes" class="w-full mt-6" :disabled="disabled" rows="6" /> -->
  <div>
    <Input
      v-if="data.status_id === 5"
      :modelValue="data.list_reason"
      @update:modelValue="(value) => handleUpdateData('list_reason', value)"
      :view="validation.list_reason.$invalid ? 'danger' : 'default'"
      type="textarea"
      label="Reason"
      class="w-full my-6"
      :disabled="disabled"
      rows="6"
    />
    <small
      class="text-red-base block mb-2"
      v-for="(error, errorIndex) of validation.list_reason.$errors"
      :key="errorIndex"
      >{{ error.$message }}.
    </small>
  </div>
  <div>
    <label for="">Email Settings</label> <br />
    <Checkbox
      :modelValue="hasEmailTemplate ? data.is_send_email : 0"
      @update:modelValue="(value) => (data.is_send_email = value)"
      :value="1"
      :value-off="0"
      :disabled="!hasEmailTemplate"
      type="toggle"
      class="mt-4"
      ><span class="toggle-label">Send Email</span></Checkbox
    >
    <Input
      v-model="data.transaction_id"
      v-show="isPaymentPending"
      class="my-4"
      label="Transaction ID"
      block
    />
  </div>
</template>

<script>
import { computed, defineComponent } from 'vue'

import { useVuelidate } from '@vuelidate/core'
import { helpers, requiredIf } from '@vuelidate/validators'

import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

export default defineComponent({
  components: {
    Input,
    Checkbox
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        list_reason: null,
        notes: '',
        returned_tracking: null,
        is_send_email: 1
      })
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:data'],
  setup(props, {emit}) {
    const isPaymentPending = computed(
      () => props.data.status_id == 7 || props.data.status_id == 3
    )
    const hasEmailTemplate = computed(() => {
      // List workflow names or id that doesn't have email template
      const workflowsWithoutEmailTemplates = [1001, 10, 2002, 1000]
      return !workflowsWithoutEmailTemplates.includes(props.data.status_id)
    })
    const rules = {
      list_reason: {
        required: helpers.withMessage('Reason is required', requiredIf(() => props.data.status_id == 5))
      }
    }
    const validation = useVuelidate(rules, props.data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleUpdateData = (fieldName, value) => {
      if(!fieldName) return
      const data = {...props.data}
      data[fieldName] = value
      emit('update:data', data)
    }
    return {
      data: props.data,
      isPaymentPending,
      hasEmailTemplate,
      validation,
      validate,
      handleUpdateData
    }
  }
})
</script>

<style lang="scss" scoped>
.toggle-label {
  font-weight: 400;
}
</style>
