<template>
  <div>
    <div v-if="isAbandonedBuyback">
      <Popover trigger="hover" placement="left" :disabled="isAbandonedBuybackStatusEditable" :width="300">
        <template #default>
          <div style="word-break: normal" class="text-left p-2">
            <p class="font-semibold">
              Insufficient details prevent the status change of an Abandoned
              Buyback Offer.
            </p>
            <p class="my-2">To change the status, you need to:</p>
          </div>
          <div class="flex flex-col">
            <Checkbox :modelValue="stateShow?.is_fulfilled_address" @click.prevent>
              <span :class="['font-normal', stateShow?.is_fulfilled_address ? 'line-through-text' : '']">Complete your order information</span>
            </Checkbox>
            <Checkbox :modelValue="stateShow?.is_fulfilled_payout" @click.prevent class="my-2">
              <span :class="['font-normal', stateShow?.is_fulfilled_payout ? 'line-through-text' : '']">Complete your payout</span>
            </Checkbox>
          </div>
        </template>
        <template #reference>
          <div>
            <Button
              :disabled="
                stateShow?.deleted_at != null || !isAbandonedBuybackStatusEditable
              "
              color="teal"
              @click="openModalEdit('update')"
              dense
              ><i class="material-icons text-base">edit</i>
            </Button>
          </div>
        </template>
      </Popover>
    </div>
    <div v-else-if="isReadonly">
      <Tooltip placement="top">
        <template #content>
          <div class="w-52 text-sm">
            You can no longer edit the status of this offer.
          </div>
        </template>
        <button
          view="smooth"
          color="grey"
          dense
          class="cursor-not-allowed py-1 px-2 text-sm leading-6 rounded-md bg-grey-01 text-grey-03"
          ><i class="material-icons text-base">edit</i>
      </button>
      </Tooltip>
    </div>
    <div v-else>
      <Button
        color="teal"
        @click="openModalEdit('update')"
        dense
        ><i class="material-icons text-base">edit</i>
      </Button>
    </div>
    <Modal v-model="modal.update" title="Update Status" @close="handleClose" :size="[5,6].includes(data.status_id) ? 'xl' : 'md'">
      <template #default>
        <div class="grid grid-cols-12 gap-6">
          <div class="border-r border-grey-01" :class="[[5,6].includes(data.status_id) ? 'col-span-2' : 'col-span-4']">
            <div class="pl-4">
              <p class="text-md font-semibold">Choose Status</p>
            </div>
            <Tab color="teal" view="smooth" v-model="data.status_id" by="id" name="short_name" :items="itemsStatus" component type="vertical" disabled-items />
          </div>
          <div :class="[[5,6].includes(data.status_id) ? 'col-span-10' : 'col-span-8']">
            <ItemTable v-if="data.status_id == 5" :show-print-label="false" :show-table-summary="false" :selectable="true" :width-column="widthColumn" @selected-items="selectedItems" buyback-type="mailin" />
            <!-- <Alert color="orange" view="smooth" class="mt-6" v-if="isSelectedItems && data.status_id === 5 && stateShowItems?.total_qty > 1">Checked item will be splited into new buyback</Alert> -->
            <StatusesForm ref="refStatusForm" :data="data" @update:data="handleUpdateData" />
          </div>
        </div>
      </template>
      <template #footer>
        <Tooltip :disabled="!disabledUpdateStatus" placement="top">
          <template #content>
            <div class="max-w-sm">{{ disabledUpdateStatusReason }}</div>
          </template>
          <template #default>
            <div class="w-max ml-auto">
              <Button
                :disabled="disabledUpdateStatus"
                color="teal"
                :loading="loading.update"
                @click="updateStatus"
                >Update Status</Button
              >
            </div>
          </template>
        </Tooltip>
      </template>
    </Modal>
    <Modal v-model="modalWarningDelivered" size="xs" hide-header hide-footer>
      <template #default>
        <div class="flex flex-col justify-center items-center py-10">
          <img src="@/assets/icon/warning.svg" alt="warning">
          <h1 class="text-3xl mt-6 font-medium">{{ stateShow?.is_multiple_shipping ? 'Some Packages Still In Transit' : 'Package Still In Transit' }}</h1>
          <p 
          v-if="stateShow?.is_multiple_shipping" 
          class="mt-4 text-center text-grey-03">
          You're marking {{ stateShow?.delivered_shipping_label_count }} of {{ stateShow?.in_transit_shipping_label_count }} package{{ stateShow?.in_transit_shipping_label_count > 1 ? 's' : '' }} as delivered. The remaining {{ stateShow?.not_used_shipping_label_count }} package{{ stateShow?.not_used_shipping_label_count > 1 ? 's' : '' }} will stay unchanged. Proceed?</p>
          <p 
          v-else
          class="mt-4 text-center text-grey-03">
          Tracking updates can be delayed — if you’ve already received the package, feel free to update the status manually.
          </p>
          <p 
          class="mt-4 text-center text-grey-03">
          Are you sure you want to proceed?
          </p>
          <div class="mt-10 flex gap-4">
            <Button color="teal" view="outlined" @click="handleCancel" style="border: none !important" :disabled="loading.update">Cancel</Button>
            <Button color="teal" @click="handleProceed" :loading="loading.update">Proceed</Button>
          </div>
        </div>
      </template>
    </Modal>
    <BuybackNotesWarning @proceed="updateStatus" />
  </div>
</template>

<script setup>
import { ref, computed, watch } from 'vue'
import { useRoute } from 'vue-router'
import { useStatuses, useShow, useShowStatuses, useShowItems } from 'composable/buyback'
import { useUser } from 'composable/user'
import { useBuybackNotes } from  '@/store/pinia/buyback/notes'
import Button from "bbUi/components/button/button.vue"
import Modal from 'bbUi/components/modal/modal.vue'
import Tab from "bbUi/components/tab/tab.vue"
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import StatusesForm from './StatusesForm'
import ItemTable from '@/components/Buyback/ItemTable.vue'
import BuybackNotesWarning from '@/components/Features/Buyback/BuybackNotes/BuybackNotesWarning.vue'

const storePiniaNotes = useBuybackNotes()
const { data, modal, loading, openModal, unsetData, actionAll, actionUpdate } = useShowStatuses('easy-offer')
const { state: stateProducts } = useShowItems('online')
const { state: stateShow, action: actionShow } = useShow('online')
const { stateOption: stateStatuses, actionStatusOptions: actionAllStatuses } = useStatuses('easy-offer', null)
const { state: stateShowItems, actionAll: actionAllItems } = useShowItems('online')
const refStatusForm = ref(null)
const modalWarningDelivered = ref(false)

const widthColumn = {
  id: '',
  product_name: '',
  condition: '',
  qty: '',
  price: '',
  total: '',
  action: ''
}

const itemsStatus = computed(() => {
  return [...stateStatuses.value].map(item => ({
    ...item,
    disabled: item.is_disabled
  }))
})

const isAbandonedBuyback = computed(() => stateShow.value?.status_id == 1000)
const isAbandonedBuybackStatusEditable = computed(() => isAbandonedBuyback.value && stateShow.value?.is_fulfilled_address && stateShow.value?.is_fulfilled_payout)
const isReadonly = computed(() => stateShow.value && stateShow.value.is_readonly)

const route = useRoute()

const openModalEdit = (target) => {
  openModal(target, () => {
    data.status_id = stateShow.value.status_id
    data.transaction_id = stateShow.value?.billing?.transaction_id
  })
}

const updateStatus = async () => {
  if(!refStatusForm.value?.validate?.()) return

  if (stateShow.value?.status_id == 2 &&
      data.status_id != 1 &&
      data.status_id != 2 && 
      ((stateShow.value?.delivered_shipping_label_count < stateShow.value?.in_transit_shipping_label_count) || stateShow.value?.not_used_shipping_label_count > 0)
    ) {
    modal.update = false
    modalWarningDelivered.value = true
    return
  }

  // 3 => FOR COMPLETED
  // 7 => FOR PAYOUT READY/PAYOUT PROCESSING
  if ([3, 7].includes(data.status_id)) {
    loading.update = true
    storePiniaNotes.warningNotes.ids = [stateShow.value?.id]
    !storePiniaNotes.warningNotes.isReviewed && await storePiniaNotes.$getNotesUnread()
    if (!!storePiniaNotes.warningNotes.notes.length) {
      storePiniaNotes.warningNotes.modal = true
      loading.update = false
      return
    }
  }

  await actionUpdate(route.params.buybackId, async () => {
    await Promise.all([
      actionAllItems(route.params.buybackId),
      actionShow(route.params.buybackId)
    ])
  })
}

const handleClose = () => {
  if (stateShow.value?.status_id == 2) {
    return
  } else {
    unsetData()
  }
}

const handleCancel = () => {
  modalWarningDelivered.value = false
  modal.update = true
}

const handleProceed = async () => {
  await actionUpdate(route.params.buybackId, async () => {
    modalWarningDelivered.value = false
    await Promise.all([
      actionAllItems(route.params.buybackId),
      actionShow(route.params.buybackId)
    ])
  })
}

watch(
  () => data.status_id,
  () => {
    if (!data.status_id) {
      return
    }
    data.ids = [] // Reset selected buyback id(s) when buyback status change
    const status = stateStatuses.value.find(v => v.id == data.status_id)
    if (status) data.is_send_email = status.is_send_email
  }
)

const selectedItems = (items) => {
  data.ids = items.map(item => item.id)
}

const handleUpdateData = (newData) => {
  Object.assign(data, newData)
}

const isSelectedItems = computed(() => data.ids.length > 0)
const isEmptySelectedNotAsDescribe = computed(() => data.status_id == 5 && data.ids.length < 1)
const disabledUpdateStatusReason = computed(() => {
  if (isEmptySelectedNotAsDescribe.value) return 'Please select at least one item'
  return 'Cannot update status'
})
const disabledUpdateStatus = computed(() => isEmptySelectedNotAsDescribe.value)

Promise.all([
  actionAll(route.params.buybackId),
  actionAllStatuses(),
  actionShow(route.params.buybackId)
])
</script>
<style lang="scss" scoped>
.line-through-text{
  @apply line-through;
}
</style>